.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 100;   
}

.header-logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}
.header-src{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}
.header-src-icon{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}
.header-src-input{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header-option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}

.header-nav{
    display: flex;
    justify-content: space-evenly;
}
.header-option-lineone{
  font-size: 10px;
}
.header-option-linetwo{
    font-size: 12px;
    font-weight: 800;
}

.header-option-basket{
    display: flex;
    align-items: center;
    color: white;
}
.header-basketcount{
    margin-left: 10px;
    margin-right: 10px;
}

.header-nav a{
    text-decoration: none;
}